import React from "react";
import { useSelector } from "react-redux";
import { addPizza } from "../../redux/cart/slice";
import { TCartPizza } from "../../redux/cart/types";
import { Link } from "react-router-dom";
import { RootStore, useAppDispatch } from "../../redux/store";

type PizzaCardProps = {
  id: string;
  title: string;
  price: number;
  imageUrl: string;
  types: number[];
  sizes: number[];
};

export const PizzaCard: React.FC<PizzaCardProps> = ({
  id,
  title,
  price,
  imageUrl,
  types,
  sizes,
}) => {
  const forms: string[] = ["", ""];
  const dispatch = useAppDispatch();
  const cartPizza = useSelector((state: RootStore) =>
    state.cart.pizzas.find((obj) => obj.id === id)
  );
  const [activeType, setActiveType] = React.useState(0);
  const [activeSize, setActiveSize] = React.useState(0);

  const addedCount = cartPizza ? cartPizza.count : 0;
  const onClickAdd = () => {
    const pizza: TCartPizza = {
      id,
      title,
      price,
      imageUrl,
      type: forms[activeType],
      size: sizes[activeSize],
      count: 0,
    };
    dispatch(addPizza({ ...pizza }));
  };

  return (
    <div className="pizza-block-centre">
      <div className="pizza-block">
        <Link to={`/pizza/${id}`}>
          <img className="pizza-block__image" src={imageUrl} alt="Pizza" />
          <h4 className="pizza-block__title">{title}</h4>
        </Link>
        <div className="pizza-block__selector">
          <ul>
            {types.map((type) => (
              <li
                key={type}
                onClick={() => setActiveType(type)}
                className={activeType === type ? "active" : ""}
              >
                {forms[type]}
              </li>
            ))}
          </ul>
          <ul>
            {sizes.map((size, i) => (
              <li
                key={size}
                onClick={() => setActiveSize(i)}
                className={activeSize === i ? "active" : ""}
              >
                {size} .
              </li>
            ))}
          </ul>
        </div>
        <div className="pizza-block__bottom">
          <div className="pizza-block__price"> {price} </div>
          <button
            onClick={onClickAdd}
            className="button button--outline button--add"
          >
            <svg
              width="12"
              height="12"
              viewBox="0 0 12 12"
              fill="none"
              xmlns="http://www.w3.org/2000/svg"
            >
              <path
                d="M10.8 4.8H7.2V1.2C7.2 0.5373 6.6627 0 6 0C5.3373 0 4.8 0.5373 4.8 1.2V4.8H1.2C0.5373 4.8 0 5.3373 0 6C0 6.6627 0.5373 7.2 1.2 7.2H4.8V10.8C4.8 11.4627 5.3373 12 6 12C6.6627 12 7.2 11.4627 7.2 10.8V7.2H10.8C11.4627 7.2 12 6.6627 12 6C12 5.3373 11.4627 4.8 10.8 4.8Z"
                fill="white"
              />
            </svg>
            <span></span>
            {addedCount ? <i>{addedCount}</i> : ""}
          </button>
        </div>
      </div>
    </div>
  );
};
