import React from "react";
import { useDispatch } from "react-redux";
import { setFilterOrder, setOrder } from "../redux/fliter/slice";
import { TSort } from "../redux/fliter/types";

type TSortProps = {
  sort: TSort;
  order: string | boolean;
};

export const sortArr: TSort[] = [
  { name: "", sort: "rating" },
  { name: "", sort: "price" },
  { name: "", sort: "title" },
];

export const Sort: React.FC<TSortProps> = React.memo(({ sort, order }) => {
  const dispatch = useDispatch();
  const [sortPopupActive, setSortPopupActive] = React.useState<boolean>(false);
  const popupRef = React.useRef<HTMLDivElement>(null);
  const onClickSort = (obj: TSort) => {
    dispatch(setFilterOrder(obj));
  };
  const onCLickSelectedItem = (value: TSort) => {
    onClickSort(value);
    setSortPopupActive(false);
  };

  React.useEffect(() => {
    const handleDisablePopup = (event: MouseEvent) => {
      const composed = event.composedPath();
      if (popupRef.current && !composed.includes(popupRef.current)) {
        setSortPopupActive(false);
      }
    };
    document.body.addEventListener("click", handleDisablePopup);
    return () => {
      document.body.removeEventListener("click", handleDisablePopup);
    };
  }, []);

  return (
    <div className="sort" ref={popupRef}>
      <div className="sort__label">
        <svg
          onClick={() => dispatch(setOrder(!order))}
          className={order ? "svgRotate" : ""}
          width="10"
          height="6"
          viewBox="0 0 10 6"
          fill="none"
          xmlns="http://www.w3.org/2000/svg"
        >
          <path
            d="M10 5C10 5.16927 9.93815 5.31576 9.81445 5.43945C9.69075 5.56315 9.54427 5.625 9.375 5.625H0.625C0.455729 5.625 0.309245 5.56315 0.185547 5.43945C0.061849 5.31576 0 5.16927 0 5C0 4.83073 0.061849 4.68424 0.185547 4.56055L4.56055 0.185547C4.68424 0.061849 4.83073 0 5 0C5.16927 0 5.31576 0.061849 5.43945 0.185547L9.81445 4.56055C9.93815 4.68424 10 4.83073 10 5Z"
            fill="#2C2C2C"
          />
        </svg>
        <b> :</b>
        <span onClick={() => setSortPopupActive(!sortPopupActive)}>
          {sort.name}
        </span>
      </div>
      <div className={sortPopupActive ? "sort__popup" : "popupNotActive"}>
        <ul>
          {sortPopupActive
            ? sortArr.map((obj, i) => (
                <li
                  onClick={() => onCLickSelectedItem(obj)}
                  key={i}
                  className={
                    sort.sort === obj.sort ? "active" : "popupNotActive"
                  }
                >
                  {obj.name}
                </li>
              ))
            : ""}
        </ul>
      </div>
    </div>
  );
});
